.closest-cruise {
  background: $color-default-white;
  color: $color-dark-text;
  padding: 95px 0 120px;

  @include vp-1023 {
    padding: 100px 45px;
  }

  @include vp-767 {
    padding: 65px 15px;
  }
}

.closest-cruise h2 {
  @include font-semibold;

  font-size: 28px;
  line-height: 42px;
  letter-spacing: 0;
  text-align: center;
  margin: 0 0 60px 0;

  @include vp-767 {
    font-size: 24px;
    line-height: 36px;
    margin: 0 0 40px 0;
  }
}

.closest-cruise__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 30px;
  max-width: 1130px;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;

  @include vp-1023 {
    width: 100%;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.closest-cruise__item {
  position: relative;
  max-width: 260px;
  height: 386px;
  background: $color-dark-text;
  background-size: cover;
  overflow: hidden;

  @include vp-1023 {
    max-width: 309px;
  }
}

.closest-cruise__item:hover {
  .closest-cruise__item-second-card {
    top: 0;
    transition: 0.5s;
  }
}

.closest-cruise__item h3 {
  @include font-medium;

  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0;
  text-align: left;
  color: $color-default-white;
  margin: 0;
  padding: 35px 25px;
  display: grid;
  align-content: end;
  width: 100%;
  height: 100%;
}

.closest-cruise__item-second-card {
  position: absolute;
  z-index: 2;
  top: 100%;
  transition: 0.5s;
  max-width: 260px;
  height: 386px;
  padding: 35px 25px;
  background: $color-dark-text;

  @include vp-1023 {
    max-width: 309px;
  }
}

.closest-cruise__item-second-card h3,
.closest-cruise__item-second-card p {
  color: $color-default-white;
}

.closest-cruise__item-second-card h3 {
  @include font-medium;

  margin: 0;
  margin-bottom: 25px;
  font-size: 18px;
  line-height: 27px;
}

.closest-cruise__item-second-card p {
  @include font-regular;

  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
}

.closest-cruise__item-second-card p span {
  @include font-medium;

  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
}

.closest-cruise__book-button {
  @include font-semibold;

  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0;
  display: flex;
  width: 210px;
  height: 40px;
  background: $color-default-white;
  justify-content: center;
  align-items: center;
  color: $color-dark-text;
}

